body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-body {
  background-color: #FAF9F6;
  margin: 1vw;
  padding: 5vw;
  margin-top: 0;
  padding-top: 2vw;
  border-radius: 12px;
}

.grid-view {
  padding: 2vw;
}

.fix-top-right {
  color: red;
  
}
.profile {
    text-align: left !important;
}

.logoutBtn {
    position: absolute;
    top: 115px;
    right: 25px;

    width: 100px;
    font-size: 15px;
}

.userInfo {
    padding-top: 25px;
    padding-bottom: 15px;
}

.username {
    font-size: 25px;
    font-weight: bold;
}

.email {
    font-size: 15px;
    font-weight: lighter;
}

.recipes {
    padding-bottom: 15px;
}

.menusCol {
    padding-top: 1rem;
}
.split-screen {
    border-radius: 5px;
    border-style: solid;
    border-width: .5px;
    border-color: #282c34;
}

.Alcoholic {
    color: red;
}
.form {
    text-align: left;
}

.row1 {
    display: flex;
    flex-direction: row;
    align-self: center;
}

#delete {
    padding-top: 30px;
}

#ingredients {
    padding-right: 10px;
}

#image {
    height: 350px;
    width: 350px;
}

#cocktail-instructions {
    width: 371px;
}

.imgButtonsAfter {
    padding-top: 10px;
    padding-left: 27px;
    padding-bottom: 15px;
    text-align: left;
}

.submit {
    padding-top: 10px;
}
